<template>
  <nav>
    <div id="logo">
      <router-link to="/app/">LOGO</router-link>
    </div>
    <div id="menu">
      <menu-item :item="item" :key="item.id" :layer="0" v-for="item in getCategories"/>
    </div>
    <div id="links">
      <router-link @click.native="scrollFix('#contact')" to="#contact">Contact</router-link>
      <a @click="showModal" class="button">Newsletter</a>
      <router-link @click.native="scrollFix('#subscribe')" to="#subscribe">Subscribe</router-link>
    </div>
    <Modal
      @close="closeModal"
      v-show="isModalVisible"
    >
      <template v-slot:header-text>NEWSLETTER</template>
      <template v-slot:body>
        <p>Join our mailing list to receive updates on new arrivals and special offers.</p>
        <p class="input"><input placeholder="Enter e-mail" type="text" v-model="email"></p>
      </template>
      <template v-slot:footer>
        <button @click="subscribed" type="button">Subscribe</button>
      </template>
    </Modal>
  </nav>
</template>

<script>
  import MenuItem from "@/components/Layout/Navigation/MenuItem";
  import { mapGetters } from "vuex";
  import Modal from "@/components/Layout/modal/Modal";
  /* eslint-disable */
  export default {
    name: "NavigationContainer",
    components: {
      Modal,
      MenuItem,
    },
    data() {
      return {
        isModalVisible: false,
        email: ""
      };
    },
    computed: {
      ...mapGetters( [ 'getCategories' ] )
    },
    methods: {
      scrollFix( hash ) {
        setTimeout( () => {
          location.href = hash
        }, 1 );
      },
      loadCategories() {
        this.$store.dispatch( 'fetchCategories' );
      },
      showModal() {
        this.isModalVisible = true;
      },
      closeModal() {
        this.isModalVisible = false;
      },
      subscribed() {
        alert( `${ this.email } now subscribing!!!` );
        this.close();
      }
    },
    mounted() {
      this.loadCategories();
    }
  }
</script>

