<template>
  <div>
    <header>
      <div>
        <p id="category-title">{{ setTitle | capitalize | removeUnderscore }}</p>
      </div>
      <CartBar />
    </header>
  </div>
</template>

<script>
  import { mapGetters } from "vuex";
  import CartBar from "@/components/Cart/CartBar";

  export default {
    name: "HeaderContainer",
    components: { CartBar },
    computed: {
      ...mapGetters(['getTitle']),
      setTitle() {
        switch(true){
          case this.$route.path.includes('product'):
            return `products | ${ this.$route.params.id }`;
          case this.$route.path === '/cart':
            return 'shopping cart';
          case this.$route.path === '/checkout':
            return 'checkout';
          case this.getTitle.length === 1 && typeof this.$route.params.category != 'undefined':
            return this.getTitle[0];
          case this.getTitle.length > 1:
            return this.getTitle.join(' | ');
          default:
            return 'welcome';
        }
      }
    },
  }
</script>

<style scoped>
  header {
    display: flex;
    justify-content: space-between;
    align-items: baseline;
    font-size: 25px;
    padding: 0.01em 16px;
  }

  #category-title {
    margin: 24px 0;
  }
</style>