<template>
  <div>
    <div style="margin: 0 16px">
      <div id="news" v-if="!$route.params.category">
        <h3>New arrivals</h3>
        <p>COLLECTION 2016</p>
        <a href="#main" class="button">SHOP NOW</a>
      </div>
      <div v-else>
        <p class="text" style="padding: 0.01em 16px;">Explore our {{ title | removeUnderscore }} collection</p>
      </div>
    </div>
    <p
      id="main"
      class="count"
      v-if="getProductsCount > 0">{{ getProductsCount }} items</p>
    <div class="products" v-if="getProductsCount > 0">
      <ProductItem
        :key="product.id"
        :product="product"
        v-for="product in getAll"
        @showModal="showModal"
      />
    </div>
    <div id="fetching" v-else>
      <p>Retrivering data for you...</p>
    </div>
    <Modal
      @close="closeModal"
      v-show="isModalVisible"
    >
      <template v-slot:header-text>GREAT CHOICE!</template>
      <template v-slot:body>
        <p>You have added 1 product to your cart.</p>
      </template>
      <template v-slot:footer>
        <button type="button" @click="closeModal">BACK TO STORE</button>
        <router-link to="/app/cart" class="button" style="margin-left: 16px;">
          SEE YOUR SHOPPING CART
          <font-awesome-icon icon="shopping-cart"></font-awesome-icon>
        </router-link>
      </template>
    </Modal>
  </div>
</template>

<script>
  import ProductItem from "@/components/Product/ProductItem";
  import { mapGetters } from 'vuex';
  import Modal from "@/components/Layout/modal/Modal";

  export default {
    name: "ProductContainer",
    components: {
      Modal,
      ProductItem
    },
    data() {
      return {
        title: this.$route.params.category,
        isModalVisible: false
      }
    },
    computed: {
      ...mapGetters( [
        'getAll',
        'getProductsCount',
        'getCategories'
      ] )
    },
    methods: {
      findCategory( arr, category ) {
        if ( arr ) {
          for ( let i = 0; i < arr.length; i++ ) {
            if ( arr[ i ].category === category ) return arr[ i ];
            let found = this.findCategory( arr[ i ].childs, category );
            if ( found ) return found;
          }
        }
      },
      getProducts() {
        if (this.$route.params.category) {

          let category = this.$route.params.category.toLowerCase();
          let searchCategory = this.findCategory( this.getCategories, category );

          let subcategories = searchCategory.subCategories;
          this.$store.dispatch( 'loadProductsFromCategory', { subcategories: subcategories } );
        } else {
          return this.$store.dispatch('loadProducts');
        }
      },
      showModal() {
        this.isModalVisible = true;
      },
      closeModal() {
        this.isModalVisible = false;
      }
    },
    mounted() {
      this.getProducts()
    }
  }
</script>

