<template>
  <div :class="{ 'click-animation' : isClicked }" class="product-item">
    <div
      :style="`background-image: url(${ product.image })`"
      @mouseleave="mouseIsOver = false"
      @mouseover="mouseIsOver = true"
      class="product-image"
    >
      <span class="badge" v-if="intPrice >= 160">New</span>
      <span class="badge" v-if="intPrice <= 70">Sale</span>
      <p
        @click="addToCart"
        @mousedown="toggleMouseClick"
        @mouseup="toggleMouseClick"
        class="clicked cart-button"
        v-if="mouseIsOver"
      >Buy now
        <font-awesome-icon icon="shopping-cart"></font-awesome-icon>
      </p>
    </div>
    <p>
      <router-link :to="`/app/${ product.category }/product/${ product.id}`">{{ product.product_name }}</router-link>
      <br/>
      <b>{{ product.price }}</b>
    </p>
  </div>
</template>

<script>
  export default {
    name: "ProductItem",
    data() {
      return {
        product: this.$attrs.product,
        mouseIsOver: false,
        isClicked: false
      }
    },
    computed: {
      intPrice() {
        return parseInt( this.product.price.slice( 1 ) );
      }
    },
    methods: {
      addToCart() {
        this.$emit('showModal');
        this.$store.dispatch( 'addToCart', { id: parseInt( this.product.id ), amount: 1 } );
      },
      toggleMouseClick() {
        this.isClicked = !this.isClicked;
      }
    }
  }
</script>

