<template>
  <div>
    <div id="product-details">
      <div id="short-info">
        <img :alt="getProduct && getProduct.product_name" :src="getProduct && getProduct.image">
        <div id="details">
          <p id="name" class="text">{{ getProduct && getProduct.product_name }}</p>
          <p id="ids" class="text-12 text-bold">PRODUCT ID: {{ getProduct && getProduct.id }}</p>
          <p id="company" class="text-18 text-bold">{{ getProduct && getProduct.company }}</p>
          <p id="short-desc" class="text-14 text-bold">{{ getProduct && getProduct.short_description }}</p>
          <p id="price" class="text-24 text-bold">{{ getProduct && getProduct.price }}</p>
        </div>
      </div>
      <div>
        <p id="desc" class="text-14 text-bold">{{ getProduct && getProduct.description }}</p>
      </div>
      <ButtonContainer
        back-text="BACK TO STORE"
        next-is="button"
        @next-link="addToCart"
        next-text="ADD TO CART"
      />
    </div>
  </div>
</template>

<script>
  import ButtonContainer from "@/components/Layout/misc/ButtonContainer";
  import { mapGetters } from "vuex";

  export default {
    name: "ProductDetails",
    components: { ButtonContainer },
    data() {
      return {
        count: 0
      }
    },
    computed: {
      ...mapGetters(['getProductById']),
      getProduct() {
        return this.getProductById( parseInt( this.$route.params.id ) );
      }
    },
    methods: {
      addToCart() {
        this.$store.dispatch( 'addToCart', { id: parseInt( this.getProduct.id ), amount: 1 } );
      },
    },
    mounted() {
      this.$store.dispatch( 'loadProductsFromCategory', { subcategories: [ this.$route.params.category ] } )
    }
  }
</script>

