<template>
  <div id="cart-bar">
    <div id="cart-link">
      <span>{{ getBuyListCount }} item{{ getBuyListCount !== 1 ? 's' : '' }}</span>
      <router-link to="/app/cart">
        <font-awesome-icon icon="shopping-cart"></font-awesome-icon>
      </router-link>
    </div>
    <div id="search">
      <div class="input">
        <input
          type="text"
          :class="{'show': showSearchBar, 'hide': !showSearchBar}"
          placeholder="Type product"
          v-model="search"
          name="search"
          @input="searchList"
        >
      </div>
      <font-awesome-icon
        @click="toggleSearch"
        class="clicked"
        icon="search"
      ></font-awesome-icon>
    </div>
  </div>
</template>

<script>
  import { mapGetters } from "vuex";

  export default {
    name: "CartBar",
    data() {
      return {
        showSearchBar: false,
        showSearch: !this.$route.params.id,
        search: "",
        cart: []
      }
    },
    computed: {
      ...mapGetters( [ 'getBuyList', 'getBuyListCount' ] )
    },
    methods: {
      toggleSearch() {
        this.showSearchBar = !this.showSearchBar;
      },
      searchList() {
        return this.$store.dispatch( 'search', this.search )
      }
    },
    mounted() {
      this.$store.watch(
        ( state, getters ) => getters.getBuyList,
        ( newValue ) => this.cart = newValue
      );
      this.searchList();
    }
  }
</script>

<style scoped>
  #cart-bar {
    display: flex;
    margin-left: auto;
  }

  #cart-link {
    cursor: pointer;
  }

  #cart-link {
    font-weight: 900;
    font-family: "Roboto", sans-serif;
    font-size: 18px;
    color: #333;
  }

  #cart-link > a {
    font-size: 25px;
  }

  #cart-link:hover {
    color: #666;
    transition: 0.3s ease-out;
  }

  #search {
    margin-left: 10px;
    display: flex;
    align-items: center;
  }

  .input > input {
    padding: 8px;
    border: 0;
    transition: all 0.5s ease-in-out;
  }

  .show {
    background-color: black;
    color: white;
    margin-right: 10px;
    width: 150px;
  }

  .show:focus {
    color: #000;
    background-color: #cecece;
  }

  .hide {
    padding: 0 !important;
    width: 0;
  }
</style>
