import Vue from 'vue';
import App from './App.vue';
import store from './store/store';
import { router } from './helpers/router';
import { library } from '@fortawesome/fontawesome-svg-core';
import {
  faShoppingCart,
  faCaretRight,
  faCaretDown,
  faCaretUp,
  faSearch,
  faMapPin,
  faPhoneAlt,
  faEnvelope,
  faCreditCard,
  faArrowRight
} from "@fortawesome/free-solid-svg-icons";
import {
  faCcAmex,
  faFacebookSquare,
  faInstagram,
  faSnapchat,
  faPinterestP,
  faTwitter,
  faLinkedinIn
} from "@fortawesome/free-brands-svg-icons";
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome';

library.add(
  faShoppingCart,
  faCaretRight,
  faCaretDown,
  faCaretUp,
  faSearch,
  faMapPin,
  faPhoneAlt,
  faEnvelope,
  faCreditCard,
  faCcAmex,
  faFacebookSquare,
  faInstagram,
  faSnapchat,
  faPinterestP,
  faTwitter,
  faLinkedinIn,
  faArrowRight
);
Vue.component('font-awesome-icon', FontAwesomeIcon);

Vue.config.productionTip = false;

Vue.filter('removeUnderscore', value => {
  let val;
  
  const replaceChar =
    (strIn,  toReplace, replaceWith) => strIn.includes(toReplace)
      ? replaceChar(strIn.replace(toReplace, replaceWith), toReplace, replaceWith)
      : strIn;
  val = replaceChar(value, '_', ' ');
  
  return `${ val }`;
});

Vue.filter('capitalize', value => {
  return `${ value[0].toUpperCase() }${ value.slice(1) }`;
});

new Vue({
  router,
  store,
  render: h => h(App),
}).$mount('#app');
