<template>
  <div class="menu-item">
    <div
      :class="{'active': isActive}"
      :style="indent"
      @click="toggleCategories"
      class="category clicked"
    >
      <router-link :to="`/app/${ item.category }`">
        <p @click="setTitle" class="category-name">
          <font-awesome-icon
            class="caret-right"
            icon="caret-right"
            v-if="showArrow"
          ></font-awesome-icon>
          {{ item.name }}
        </p>
      </router-link>
      <font-awesome-icon
        :icon="showChildren ? 'caret-up' : 'caret-down'"
        style="margin-left: 10px;"
        v-if="item.childs.length > 0"
      ></font-awesome-icon>
    </div>
    <div :style="item.childs.length > 0 && {marginTop: '5px'}" v-if="showChildren">
      <menu-item
        :item="child"
        :key="child.id"
        :layer="layer + 1"
        v-for="child in item.childs"
      />
    </div>
  </div>
</template>

<script>
  import { mapGetters } from "vuex";

  export default {
    props: [ 'item', 'layer' ],
    name: "MenuItem",
    data() {
      return {
        showChildren: false
      }
    },
    computed: {
      ...mapGetters( [ 'getTitle' ] ),
      indent() {
        return { "margin": `0 ${ this.layer * 24 }px` }
      },
      isActive() {
        return this.item.category === this.$route.params.category
      },
      showArrow() {
        return this.item.category === this.$route.params.category
      },
      getTitleFromCategory() {
        if ( this.layer ) {
          let parent = this;
          let title = [ this.$props.item.category ];
          for ( let i = 0; i < this.layer; i++ ) {
            parent = parent.$parent;
            title = [ parent.$props.item.category, ...title ]
          }
          return title;
        }
        return this.$props.item.category;
      }
    },
    methods: {
      toggleCategories() {
        if ( this.item.childs.length > 0 ) this.showChildren = !this.showChildren;
      },
      setTitle() {
        return this.$store.dispatch( 'setTitle', { title: this.getTitleFromCategory } )
      }
    },
    mounted() {
      this.$route.params.category === this.item.category && this.setTitle();
      this.showChildren = this.getTitle.includes( this.$props.item.category )
    }
  }
</script>

<style scoped>
  a {
    text-decoration: none;
    color: inherit;
    width: 100%;
  }

  .category {
    padding: 10px 24px;
  }

  .category:hover {
    background-color: #ccc;
    color: black;
    transition: 0.3s ease-out;
  }

  .active {
    background-color: whitesmoke;
    color: black;
  }

  .category-name {
    margin: 0;
    padding: 0;
    display: inline-block;
  }

  .caret-right {
    margin-left: 0;
    margin-right: 16px;
    font-size: 15px;
  }
</style>
