<template>
  <div id="cart-container">
    <div id="cart">
      <p class="text">Chosen products</p>
      <div id="cart-list" v-if="cart.length > 0">
        <div :id="product.id" :key="product.id" class="cart-item" v-for="product in cart">
          <div>
            <p class="text text-bold count">{{ product.amount }} x </p>
            <img :src="product.image" class="product-image">
            <router-link :to="`/app/${ product.category }/product/${ product.id}`" class="product-details">
              <p class="product-name text-bold">{{ product.product_name }}</p>
              <p class="priceCraft">{{ product.price }}/pc.</p>
              <p class="price text-bold">${{ (parseFloat(product.price.substr(1)) * product.amount).toFixed(2) }}</p>
            </router-link>
          </div>
          <p @click="removeFromCart(product)" class="button button-small">REMOVE</p>
        </div>
      </div>
      <div class="cart-item" v-else>None</div>
      <ButtonContainer
        backText="BACK TO STORE"
        nextLink="checkout"
        nextText="CHECKOUT"
      />
    </div>
  </div>
</template>

<script>
  import { mapGetters } from "vuex";
  import ButtonContainer from "@/components/Layout/misc/ButtonContainer";

  export default {
    name: "CartContianer",
    components: { ButtonContainer },
    data() {
      return {
        cart: []
      }
    },
    computed: {
      ...mapGetters( [ 'getBuyList', 'getBuyListCount' ] ),
    },
    methods: {
      removeFromCart (product) {
        if ( product.amount === 1 || product.amount === 0 )
          return this.$store.dispatch( 'removeFromCart', { id: product.id } );
        return this.$store.dispatch( 'decrement', { id: product.id } );
      }
    },
    mounted() {
      this.$store.watch(
        ( state, getters ) => getters.getBuyList,
        ( newValue ) => this.cart = newValue
      );
      this.$store.dispatch( 'initialBuyList' );
      this.$store.dispatch( 'initialProducts' );
    }
  }
</script>

