<template xmlns:v-slot="http://www.w3.org/1999/XSL/Transform">
  <div>
    <div id="checkout">
      <div id="title"><p class="text">Checkout & Payment</p></div>
      <div id="order">
        <div id="buy-list">
          <div :key="product.id" class="list-item" v-for="product in getBuyList">
            <div class="details">
              <p class="text-16 text-bold count">{{ product.amount }} x </p>
              <p class="text-16 text-bold">{{ product.product_name }}</p>
              <p class="per-craft">({{ product.price }}/pc.)</p>
            </div>
            <p class="text-16 text-bold">${{ (parseFloat(product.price.substr(1)) * product.amount).toFixed(2) }}</p>
          </div>
        </div>
        <div class="total">
          <p class="text-12" style="margin-right: 16px">TOTAL:</p>
          <p class="text-24 text-bold">${{ sum }}</p>
        </div>
      </div>
      <div id="payment">
        <p class="text text-bold">Enter your addres and credit card data</p>
        <div id="payment-data">
          <form id="payment-form">
            <div id="address">
              <p class="text-18 text-bold">Your address</p>
              <div class="label-block">
                <label for="name">Name</label>
                <input
                  id="name"
                  name="name"
                  placeholder="John Doe"
                  required
                  type="text"
                  v-model="customer.name"
                >
              </div>
              <div class="label-block">
                <label for="phone">Phone</label>
                <masked-input
                  id="phone"
                  mask="1-111-111-111"
                  placeholder="0-100-200-300"
                  required
                  v-model="customer.phone"
                />
              </div>
              <div class="label-block">
                <label for="postal-code">Postal Code</label>
                <masked-input
                  id="postal-code"
                  mask="11-111"
                  placeholder="10-200"
                  required
                  v-model="customer.postalCode"
                />
              </div>
              <div class="label-block">
                <label for="town">Town</label>
                <input
                  id="town"
                  name="town"
                  placeholder="New York"
                  required
                  type="text"
                  v-model="customer.town"
                >
              </div>
              <div class="label-block">
                <label for="street">Street</label>
                <input
                  id="street"
                  name="street"
                  placeholder="1st Street Avenue"
                  required
                  type="text"
                  v-model="customer.street"
                >
              </div>
            </div>
            <div id="creadit-card">
              <p class="text-18 text-bold">Credit card</p>
              <div class="label-block">
                <label for="number">Number</label>
                <masked-input
                  id="number"
                  mask="1111 1111 1111 1111"
                  placeholder="---- ---- ---- ----"
                  required
                  v-model="customer.cardNumber"
                />
              </div>
              <div class="label-block">
                <label for="expiry-date">Expiry Date</label>
                <masked-input
                  id="expiry-date"
                  mask="11/1111"
                  placeholder="--/----"
                  required
                  v-model="customer.expiry"
                />
              </div>
              <div class="label-block">
                <label for="cvv">CVV</label>
                <masked-input
                  id="cvv"
                  mask="111"
                  placeholder="---"
                  required
                  v-model="customer.cvv"
                />
              </div>
            </div>
          </form>
        </div>
      </div>
      <ButtonContainer
        @next-link="showModal"
        back-text="BACK TO SHOPPING CART"
        next-is="button"
        next-text="ORDER & PAY"
      />
      <Modal
        @close="closeModal"
        v-show="isModalVisible"
      >
        <template v-slot:header-text>ORDER & PAY</template>
        <template v-slot:body>
          <h2>YOUR ORDER: </h2>
          <div id="buy-list-short" style="width: 60%;">
            <div :key="`a-${ product.id }`" class="list-item" v-for="product in getBuyList">
              <div class="details">
                <p class="text-14 text-bold count">{{ product.amount }} x </p>
                <p class="text-14 text-bold">{{ product.product_name }}</p>
                <p class="text-12 per-craft">({{ product.price }}/pc.)</p>
              </div>
              <p class="text-14 text-bold">${{ (parseFloat(product.price.substr(1)) * product.amount).toFixed(2) }}</p>
            </div>
          </div>
        </template>
        <template v-slot:footer>
          <div style="text-align: center">
            <h2>Was sent to: </h2>
            <p>{{ customer.name }}</p>
            <p>{{ customer.phone }}</p>
            <p>{{ customer.street }}</p>
            <p>{{ customer.postalCode }} {{ customer.town }}</p>
          </div>
        </template>
      </Modal>
    </div>
  </div>
</template>

<script>
  import ButtonContainer from "@/components/Layout/misc/ButtonContainer";
  import { mapGetters } from "vuex";
  import Modal from "@/components/Layout/modal/Modal";
  import MaskedInput from 'vue-masked-input';

  export default {
    name: "Checkout",
    components: { Modal, ButtonContainer, MaskedInput },
    data() {
      return {
        customer: {
          name: '',
          phone: '',
          town: '',
          postalCode: '',
          street: '',
          cardNumber: '',
          expiry: '',
          cvv: ''
        },
        isModalVisible: false
      }
    },
    computed: {
      ...mapGetters( [ 'getBuyList' ] ),
      sum() {
        let sum = 0;
        this.getBuyList.forEach( item => sum += parseFloat( item.price.substr( 1 ) ) * item.amount );
        return sum.toFixed( 2 );
      }
    },
    methods: {
      showModal() {
        const form = document.querySelector( '#payment-form' );
        if ( form.checkValidity() )
          this.isModalVisible = true;
        else
          form.reportValidity();
      },
      closeModal() {
        this.isModalVisible = false;
        this.customer = {
          name: '',
          phone: '',
          town: '',
          postalCode: '',
          street: '',
          cardNumber: '',
          expiry: '',
          cvv: ''
        };
        document.querySelector( '#payment-form' ).reset();
        this.$store.dispatch( 'removeAll' );
      }
    }
  }
</script>

