import Vue from 'vue';
import Vuex from 'vuex';
import Api from "@/services/Api";
import VuexPersist from 'vuex-persist';
import { categories } from '../../public/data/categories';

const vuexPersist = new VuexPersist({
  key: 'shop',
  storage: localStorage
});

Vue.use(Vuex);

let arrToTree = array => {
  let tree = [], mappedArray = [];
  
  array.forEach(item => mappedArray.push({ ...item, childs: [] }));
  mappedArray.forEach(item => item.parentId
    ? mappedArray.find(i => i.id === item.parentId).childs.push(item)
    : tree.push(item));
  
  return tree.sort((a, b) => a.name > b.name);
};

const getDataApi = Api().get('5d2f1f34340000100064d664');

const state = {
  products: [],
  buyList: [],
  search: '',
  categories: [],
  title: []
};

const getters = {
  getAll: state => {
    return (state.search === "")
      ? state.products
      : state.products.filter(product => product.product_name.toLowerCase().includes(state.search.toLowerCase()));
  },
  getProductsCount: (state, getters) => {
    return getters.getAll.length;
  },
  getProductById: state => id => {
    return state.products.find(item => item.id === id);
  },
  getBuyList: state => {
    return state.buyList;
  },
  getBuyListCount: (state, getters) => {
    let itemAmount = 0;
    getters.getBuyList.forEach(item => itemAmount += item.amount);
    return itemAmount;
  },
  getCategories: state => {
    return state.categories;
  },
  getTitle: state => {
    return state.title;
  }
};

const mutations = {
  fetchCategories: (state, payload) => {
    state.categories = payload.categories;
  },
  initialBuyList: state => {
    if (localStorage.getItem('shop')) {
      state.buyList = JSON.parse(localStorage.getItem('shop')).buyList;
    }
  },
  initialProducts: state => {
    if (localStorage.getItem('shop')) {
      state.products = JSON.parse(localStorage.getItem('shop')).products;
    }
  },
  fetchProducts: (state, payload) => {
    state.products = payload.products;
  },
  addToCart: (state, payload) => {
    let product = state.products.find(item => item.id === parseInt(payload.id));
    let found = state.buyList.find(item => item.id === product.id);

    if (found) {
      let index = state.buyList.findIndex(item => item.id === product.id);
      state.buyList.splice(index, 1, { ...found, amount: found.amount + parseInt(payload.amount) });
    } else {
      state.buyList.push({ ...product, amount: parseInt(payload.amount) });
    }
  },
  decrement: (state, payload) => {
    let found = state.buyList.find(item => item.id === parseInt(payload.id));
    let index = state.buyList.findIndex(item => item.id === parseInt(payload.id));
    found.amount > 0 && state.buyList.splice(index, 1, { ...found, amount: found.amount - 1});
  },
  removeFromCart: (state, payload) => {
    state.buyList = state.buyList.filter(item => item.id !== parseInt(payload.id));
  },
  removeAll: state => {
    state.buyList = [];
  },
  search: (state, payload) => {
    state.search = payload;
  },
  setTitle: (state, payload) => {
    state.title = typeof payload.title == "string" ? [payload.title] : payload.title;
  },
  clearTitle: state => {
    state.title = [];
  }
};

const actions = {
  fetchCategories: context => {
    context.commit('fetchCategories', { categories: arrToTree(categories) })
  },
  loadProducts: async context => {
    let data = await getDataApi.then(json => json.data);
    context.commit('fetchProducts', { products: data })
  },
  loadProductsFromCategory: async (context, payload) => {
    let prod = [];
    await getDataApi
      .then(json => json.data)
      .then(products => products.forEach(product => {
        payload.subcategories.includes(product.category) && prod.push(product)
      }));
    context.commit('fetchProducts', { products: prod });
  },
  addToCart: (context, payload) => {
    context.commit('addToCart', payload);
  },
  decrement: (context, payload) => {
    context.commit('decrement', payload);
  },
  initialBuyList: context => {
    context.commit('initialBuyList');
  },
  initialProducts: context => {
    context.commit('initialProducts');
  },
  removeFromCart: (context, payload) => {
    context.commit('removeFromCart', payload);
  },
  removeAll: context => {
    context.commit('removeAll');
  },
  search: (context, payload) => {
    context.commit('search', payload);
  },
  setTitle: (context, payload) => {
    context.commit('setTitle', payload);
  },
  clearTitle: context => {
    context.commit('clearTitle');
  }
};

const store = new Vuex.Store({
  state,
  getters,
  mutations,
  actions,
  plugins: [vuexPersist.plugin]
});

export default store;