<template>
  <div id="app">
    <NavigationContainer class="unselectable"/>
    <div id="content">
      <HeaderContainer />
      <router-view :key="$route.fullPath"></router-view>
      <FooterContainer/>
    </div>
  </div>
</template>

<script>
  import NavigationContainer from "@/components/Layout/Navigation/NavigationContainer";
  import FooterContainer from "@/components/Layout/Footer/FooterContainer";
  import HeaderContainer from "@/components/Layout/Header/HeaderContainer";
  import { mapActions } from "vuex";
  /* eslint-disable */
  export default {
    name: 'app',
    components: { HeaderContainer, FooterContainer, NavigationContainer },
    methods: {
      scrollFix(hash) {
        setTimeout(() => { location.href = hash }, 1);
      },
      ...mapActions(['clearTitle'])
    },
    mounted() {
      if (this.$route.hash) {
        setTimeout(() => this.scrollFix(this.$route.hash), 1);
      }
      if (this.$route.path === '/') this.clearTitle();
    },
    updated() {
      if (this.$route.path === '/') this.clearTitle();
    }
  }
</script>

