import Vue from 'vue';
import Router from 'vue-router';
import ProductContainer from "@/components/Product/ProductContainer";
import ProductDetails from "@/components/Product/ProductDetails";
import CartContianer from "@/components/Cart/CartContianer";
import Checkout from "@/components/Cart/Checkout";

Vue.use(Router);

export const router = new Router({
  mode: 'history',
  routes: [
    { path: '/app/', component: ProductContainer, props: true },
    { path: '/app/cart', component: CartContianer, props: true },
    { path: '/app/checkout', component: Checkout, props: true },
    { path: '/app/:category/product/:id', component: ProductDetails, props: true },
    { path: '/app/:category', component: ProductContainer, props: true },
    { path: '*', redirect: '/app'}
  ]
});

router.beforeEach((to, from, next) => {
  return next();
});
